	module.exports = {"data":{"post":{"html":"<blockquote>The corporation insists on an elaborate aptitude test for new employees that is intended to weed out troublemakers. When Barbara Ehrenreich took the test at the Minneapolis Wal-Mart, she was told that she had given a wrong answer when she agreed \"strongly\" with the proposition that \"rules have to be followed to the letter at all times.\" The only acceptable answer for Wal-Mart was \"very strongly.\" Similarly, the only correct answer to the proposition \"there is room in every corporation for a non-conformist\" was: \"totally disagree.\"</p></blockquote>\n<p><a title=\"The New York Review of Books: Inside the Leviathan\" href=\"http://www.nybooks.com/articles/17647\">The New York Review of Books: Inside the Leviathan</a>, an essay on WalMart. <a title=\"Davos Newbies : Davos Newbies Home\" href=\"http://www.davosnewbies.com/2004/12/07#theWalmartModel\">(via Davos Newbies)</a></p>","fields":{"title":"Strongly is not strong enough","date":"09 Dec 2004","url":"https://papascott.de/2004-12-09-strongly-is-not-strong-enough/"}}},"pathContext":{"slug":"/2004-12-09-strongly-is-not-strong-enough/","prev":{"fields":{"title":"Sock Puppet","date":"09 Dec 2004","slug":"/2004-12-09-sock-puppet/"}},"next":{"fields":{"title":"Truly, Deeply, Madly","date":"09 Dec 2004","slug":"/2004-12-09-truly-deeply-madly/"}}}}

